@media screen and (max-width: $tablet-portrait-down) {
  #store {
    .header {
      background-image: url("#{$image-path}fundo-header.png");
      background-position: center center;
      background-size: cover;
      max-width: 100%;
      position: relative;

      .wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        position: relative;

        .localization {
          button {
            background: none;
            border: none;
          }

          width: rem(24);
          height: rem(16);
          top: rem(35.63);
          right: 0;
          position: absolute;
        }

        .arrow-back {
          width: rem(23);
          height: rem(16);
          top: rem(35.63);
          left: 0;
          position: absolute;
        }

        .logo-content {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          gap: rem(24);

          .logo {
            margin: rem(66) 0 0;
          }

          .title-logo {
            margin-bottom: rem(24);

            .title {
              color: white;
              text-align: center;
            }
          }
        }

        .search {
          width: 100%;
          padding-bottom: rem(31.92);

          label {
            position: relative;

            &::before {
              content: "";
              position: absolute;
              background: url("#{$image-path}icon/lupa.svg");
              background-position: center;
              background-repeat: no-repeat;
              background-size: cover;
              width: rem(12);
              height: rem(12);
              top: rem(5);
              left: rem(20);
            }
          }

          input {
            width: 100%;
            height: rem(60);
            border-radius: rem(10);
            padding: rem(12) 0 rem(12) rem(44);
            max-height: rem(45);
            border: none;

            &::placeholder {
              color: map-get($color, gray_alt);
              font-size: rem(16);
              line-height: rem(25);
            }

            &::-webkit-search-cancel-button {
              position: relative;
              right: 20px;
              -webkit-appearance: none;
              height: 20px;
              width: 20px;
              border-radius: 10px;
              background: map-get($color, red);
            }
          }
        }
      }
    }
  }

  #access {
    .header {
      .logo {
        display: flex;
        justify-content: center;
        align-items: center;
        max-width: rem(160);

        img {
          width: 100%;
        }
      }
    }
  }
}
