@media screen and (max-width: $tablet-portrait-down) {
  .access,
  .login {
    width: 100%;

    .wrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;

      .title-access,
      .title-login {
        .title {
          text-align: center;
        }
      }

      .open {
        width: 100%;

        .open-code {
          .button-code {
            display: block;
          }

          input {
            display: none;
          }
        }

        .button-code {
          margin-top: rem(20);
          background: map-get($color, red);
          padding: rem(15) 0;
          border-radius: rem(6);
          border: none;
          width: 100%;
          text-align: center;
          color: map-get($color, white);

          a {
            color: map-get($color, white);
            text-decoration: none;
          }
        }

        .or {
          margin: rem(30) 0;
          text-align: center;
        }

        .form-code {
          input {
            width: 100%;
            background: map-get($color, white_alt_2);
            border: 1px solid map-get($color, gray_alt_2);
            border-radius: rem(6);
            padding: rem(15) 0;
            text-align: center;

            &::placeholder {
              color: map-get($color, gray_alt_2);
            }
          }
          input[type="password"] {
            margin-top: rem(10);
          }
        }
      }
    }
  }

  .login {
    .title-login {
      h2 {
        font-weight: 600;
        color: map-get($color, black_alt_2);
      }
    }

    .form-code {
      margin-top: rem(10);
    }
  }
}
