@media screen and (max-width: $tablet-portrait-down) {
  .header-internal-screen {
    margin-bottom: rem(16);

    .wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: rem(12);

      .logo-content {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: rem(16);

        .logo {
          max-width: rem(46);
          max-height: rem(46);

          img {
            border-radius: rem(6);
            object-fit: cover;
            width: 100%;
          }
        }

        .title-logo {
          .title {
            font-weight: 600;
          }
        }
      }

      .search-menu {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: rem(20);

        .search {
          label {
            color: map-get($color, red);
            display: flex;
            align-items: center;
            justify-content: flex-end;
            margin-right: rem(25.97);
            position: relative;

            &::before {
              content: "";
              background: url("#{$image-path}icon/magnifier-red.svg");
              background-position: center center;
              background-size: cover;
              background-repeat: no-repeat;
              right: rem(-25.71);
              width: rem(17.73);
              height: rem(18.82);
              position: absolute;
            }
          }

          input {
            display: none;
            width: 100%;
            height: rem(60);
            background: map-get($color, white_alt_3);
            border-radius: rem(5);
            padding: rem(12) 0 rem(12) rem(44);
            max-height: rem(45);
            border: none;

            &::placeholder {
              color: map-get($color, gray_alt);
              font-size: rem(16);
              line-height: rem(25);
            }

            &::-webkit-search-cancel-button {
              position: relative;
              right: 20px;
              -webkit-appearance: none;
              height: 20px;
              width: 20px;
              border-radius: 10px;
              background: map-get($color, red);
            }
          }
        }

        .menu {
          button {
            background: none;
            border: none;
          }

          width: rem(24);
          height: rem(16);
          top: rem(35.63);
          right: 0;
        }
      }
    }

    &.search-active {
      .wrapper {
        .logo-content {
          display: none;
        }
        .search-menu {
          width: 100%;

          .search {
            animation: animationSearch 0.3s linear forwards;
            position: relative;

            &::before {
              content: "";
              position: absolute;
              background: url("#{$image-path}icon/lupa.svg");
              background-repeat: no-repeat;
              background-size: cover;
              height: rem(11);
              width: rem(11);
              top: 35%;
              left: 5%;
            }

            @keyframes animationSearch {
              to {
                width: 100%;
              }

              from {
                width: 0%;
              }
            }

            label {
              display: none;
            }
            input {
              display: block;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: $mobile-only) {
  .header-internal-screen {
    .wrapper {
      .logo-content {
        .title-logo {
          .title {
            font-size: rem(12);
          }
        }
      }
      .search-menu {
        .search {
          label {
            font-size: rem(12);
          }
        }
      }
    }
  }
}
