.card {
  display: flex;
  gap: rem(20);
  align-items: center;
  border-radius: rem(20);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.29);
  margin-top: rem(20);

  .dish {
    max-height: rem(120);
  }

  .content {
    .title {
      &.subt-3 {
        color: map-get($color, purple);
        margin-bottom: rem(5);
      }
    }

    .paragraph {
      color: map-get($color, gray);
      text-overflow: ellipsis;
      margin-bottom: rem(5);
    }

    .price {
      color: map-get($color, green-2);
    }
  }
}
