.title {
  &.main {
    font-size: rem(20);
    line-height: rem(43);
    font-weight: 600;
  }

  &.main-regular {
    font-size: rem(26);
    line-height: rem(43);
  }

  &.subt-2 {
    font-size: rem(20);
    line-height: rem(30);
  }

  &.subt-2-regular {
    font-size: rem(26);
    line-height: rem(32);
  }

  &.subt-3 {
    font-size: rem(25);
    line-height: rem(38);
  }

  &.subt-4 {
    font-size: rem(22);
    line-height: rem(33);
  }

  // &.subt-4 {
  //   font-size: rem(20);
  //   line-height: rem(30);
  // }
}

