#localization-page {
  background: map-get($color, black);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .header {
    align-self: center;
    justify-self: center;

    .closer {
      position: absolute;
      right: rem(20);
      top: rem(28);
      font-weight: bold;
      font-size: rem(26);

      a {
        color: map-get($color, white);
        text-decoration: none;
      }
    }

    .logo-content {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: rem(22);

      .title-logo {
        color: map-get($color, white);
        text-align: center;

        h2 {
          font-weight: 600;
        }
      }
    }
  }

  .localization {
    margin: rem(70) 0;

    .title-localization {
      color: map-get($color, white);
      text-align: center;
      font-family: "Segoe UI", sans-serif;
      font-size: rem(26);
      line-height: rem(32);
      position: relative;
      display: flex;
      justify-content: flex-start;

      &::before {
        content: "";
        position: absolute;
        background: url("#{$image-path}icon/localization.png");
        width: rem(21);
        height: rem(21);
        left: -15px;
      }
    }
  }

  .maps {
    .wrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: rem(16);
    }
  }
}
