.request {
  padding: rem(20) 0 0;
  background: map-get($color, white_alt);

  .request-selected {
    .photo-request {
      max-width: rem(348);
      max-height: rem(234);
      margin: 0 auto rem(30) auto;

      img {
        object-fit: cover;
        width: 100%;
        border-radius: rem(10);
      }
    }

    .content {
      .title-request {
        color: map-get($color, black_alt);
        margin-bottom: rem(5);
      }

      .description-request {
        font-family: "Segoe UI", sans-serif;
        color: map-get($color, black_alt_2);
        display: flex;
        flex-direction: column;
        gap: rem(18);

        span {
          color: map-get($color, gray_alt_2);
        }
      }

      .price {
        margin: rem(20) 0 rem(28);
      }

      .ask-too {
        text-align: center;
        color: map-get($color, black_alt_2);
        border-top: 1px solid map-get($color, gray_alt_2);
        padding: rem(25) 0;

        h2 {
          font-weight: bold;
        }
      }
    }
  }

  .list-of-requests {
    background: map-get($color, white_alt_2);
  }
}
