@media screen and (max-width: $tablet-portrait-down) {
  .category-selected {
    background: map-get($color, red);
    width: 100%;

    .wrapper {
      display: flex;
      align-items: center;
      gap: rem(26.56);
      padding: rem(16) 0;

      .arrow-back {
        button {
          border: none;
          background: none;
        }
      }

      .title-category-selected {
        color: map-get($color, white);
      }
    }
  }
}
