@media screen and (max-width: $tablet-portrait-down) {
  .footer {
    width: 100%;
    background: map-get($color, white_alt);
    padding: rem(20) 0 rem(20);

    .copy {
      text-align: center;
      text-transform: uppercase;

      a {
        text-decoration: none;
        color: map-get($color, black);
      }

      .paragraph {
        font-weight: 600;
      }
    }
  }
}
