@media screen and (max-width: $tablet-portrait-down) {
  .list-of-category {
    background: map-get($color, white_alt);
    padding: rem(30) rem(0) rem(20);

    .title-content {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: rem(16);
      margin: 0 0 rem(30);

      .title {
        display: flex;
        align-items: center;
        position: relative;

        .subt-2 {
          color: map-get($color, gray);
        }
      }
    }

    .nav {
      .nav-category {
        .item-category {
          background: map-get($color, red);
          border-radius: rem(10);

          a {
            display: block;
            border: none;
            background: none;
            color: map-get($color, white);
            text-decoration: none;
            text-align: center;
            width: 100%;
            padding: rem(16) 0;
          }

          + .item-category {
            margin-top: rem(20);
          }
        }
      }
    }
  }
}
