.list-of-requests {
  padding: rem(40) 0 rem(20);
  background: map-get($color, white_alt);

  .row-content {
    .row {
      + .row {
        margin-top: rem(16);
      }

      a {
        display: flex;
        gap: rem(16);
        align-items: stretch;
        border-bottom: 1px solid map-get($color, gray_alt_2);
        text-decoration: none;

        .photo-request {
          width: rem(75);
          height: rem(75);

          img {
            object-fit: cover;
            border-radius: rem(10);
          }
        }

        .content {
          margin-bottom: rem(12);
          width: 100%;

          .title {
            color: map-get($color, black_alt);
          }

          .description-request {
            color: map-get($color, black_alt_2);
            overflow: hidden;
          }

          .price-people {
            display: flex;
            align-items: baseline;
            justify-content: space-between;

            .people {
              font-size: rem(16);
              line-height: rem(25);
              font-weight: 400;
              color: map-get($color, gray_alt_2);
            }
          }
        }
      }
    }
  }
}
