#access,
#login {
  display: grid;
  grid-template-rows: 1fr 2fr 1fr;

  .header {
    align-self: center;
    justify-self: center;
  }

  .access {
    align-self: center;
    justify-self: center;
  }

  .login {
    align-self: center;
    justify-self: center;
  }

  .footer {
    align-self: end;
    justify-self: center;
    background: transparent;
  }
}
