@font-face {
  font-family: "Poppins";
  src: url("#{$font-path}subset-Poppins-Bold.eot");
  src: url("#{$font-path}subset-Poppins-Bold.eot?#iefix") format("embedded-opentype"),
    url("#{$font-path}subset-Poppins-Bold.woff2") format("woff2"), url("subset-Poppins-Bold.woff") format("woff"),
    url("#{$font-path}subset-Poppins-Bold.ttf") format("truetype"),
    url("subset-Poppins-Bold.svg#Poppins-Bold") format("svg");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

